.gallery-container,
.comment-container {
  // max-height: 550px;
  ul {
    list-style: none;
    padding: 0;
  }
  .hs {
    display: grid;
    grid-gap: 1rem;
    grid-template-rows: minmax(80vh, 1fr);
    grid-template-columns: repeat(3, 1fr);
    // grid-template-columns: 120px 120px repeat(auto-fill, 120px);
    // grid-template-rows: repeat(auto-fill, 150px);
    // grid-auto-flow: column;
    // grid-column-end: 3;
    // grid-auto-columns: calc(10%);
    // overflow-x: scroll;
    // overflow: hidden;
    // scroll-snap-type: x mandatory;
    @media (max-width: 1250px) {
      // grid-auto-columns: calc(40%);
      grid-template-columns: repeat(1, 1fr);
    }
    .item {
      scroll-snap-align: center;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-start;
      // padding-bottom: 5px;
      padding: 10px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      position: relative;
      max-height: 80vh;
      border-radius: 15px;
      &:first-child {
        // grid-column: 1 / 4;
        // grid-row: 1 / 3;
      }
      img {
        width: 100%;
        height: 100%;
      }
      &.booksy-wrapper {
        background-position: center;
        background-size: 20%;
        background-color: #0ba3ad;
        // border:solid 1px black;
        &.item {
          justify-content: flex-end;
        }
        .booksy-button {
          font-family: $font-family-base;
          font-weight: 900;
          cursor: pointer;
          display: inline-block;
          margin: 0 auto;
          padding: 0;
          width: 180px;
          height: 44px;
          transition: all 0.5s ease 0s;
          border-radius: 8px;
          -moz-border-radius: 8px;
          -webkit-border-radius: 8px;
          z-index: 4;
          background-color: #a5f5f2;
          color: white;
          border: solid 1px white;
          &:hover {
            transform: scale(1.05);
            transition: all 0.5s ease 0s;
          }
        }
        .booksy {
          position: absolute;
          height: 80px;
          top: 4%;
          filter: brightness(100);
          z-index: 3;
        }
        .phone {
          position: absolute;
          // width: 250px !important;
          height: 550px;
          width: auto;
          rotate: 0deg;
          bottom: 10%;
          right: auto;
          left: auto;
          top: auto;
          z-index: 0 !important;
          @media (max-width: 415px) {
            opacity: 0;
          }
        }
      }
      &.about-wrapper {
        @extend .booksy-wrapper;
        background-color: #ff7f85;
        background-size: cover;
      }
      &.contact-wrapper {
        @extend .booksy-wrapper;
        background-color: #ff5379;
        // border:solid 2px #ffb1b1;
        background-size: cover;
        color: white;
        @media (max-width: 415px) {
          opacity: 0;
        }
      }
      span {
        font-family: $font-family-logo;
        font-weight: 900;
        font-size: 30px;
        background-color: rgb(255, 255, 255);
        padding:5px 20px;
        // width:250px;
        border-radius: 10px;
        a {
          color:black;
        }
        &.makeup {
          color: #000000 !important;
        }
        &.nails {
          color: rgb(0, 0, 0);
        }
        &.course {
          color: rgb(0, 0, 0);
        }
        @media (max-width: 1250px) {
          // font-size: 20px;
        }
      }
      &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
      }
      &::-webkit-scrollbar-track {
        border-radius: 10px;
        background: rgba(0, 0, 0, 0.1);
      }
      &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background: rgba(0, 0, 0, 0.2);
      }
      &::-webkit-scrollbar-thumb:hover {
        background: rgba(0, 0, 0, 0.4);
      }
      &::-webkit-scrollbar-thumb:active {
        background: rgba(0, 0, 0, 0.9);
      }
    }
  }
  .gallery-carousel {
    img {
      max-height: 80vh;
      object-fit: cover;
      border-radius: 10px;
    }
  }
}
