h4 {
  font-family: $font-family-logo !important;
  a {
    font-family: $font-family-logo !important;
  }
}
.btn-primary {
  height: 40px;
  background-color: #fc405c;
  color: white;
  text-transform: uppercase;
  border: solid white 2px;
  border-radius: 10px;
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  &:disabled {
    background-color: $light-gray;
    border-color: $dark-gray;
  }
  &:hover {
    border-color: $secondary;
    color: black !important;
  }
}
.copyright,
.top-header {
  min-height: 50px;
  background-color: black;
  color: $primary;
}

h2 {
  text-transform: uppercase;
}

p {
  font-size: 12px;
  font-weight: 900;
}

.wpcf7-spinner {
  display: none;
}

.entry-title {
  font-family: "Oswald";
  font-weight: 900;
  font-size: 30px;
}

.entry-content {
  font-weight: 900;
  background-color: rgb(255, 255, 255);
  padding: 5px 20px;
  max-width: 40%;
}
.card {
  border: none;
  border-radius: 0px;
  height: 100%;
  img {
    border-radius: 0px;
    height: 100%;
  }
}
.card.video {
  height: 100%;
  @media (max-width: 576px) {
    height: 550px;
  }
}
.slider {
  z-index: 0;
}
.card-group {
  height: 230px;
  @media (max-width: 576px) {
    height: unset;
  }
}
.card-img-overlay {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.instagram-section {
  color: white;
  background: #f09433;
  background: linear-gradient(
    230deg,
    #405de6,
    #5851db,
    #833ab4,
    #c13584,
    #e1306c,
    #fd1d1d
  );
  border-radius: 10px;
  img {
    width: 60px;
    filter: invert(1);
  }
  a {
    height: 100%;
    width: 100%;
  }
}
#sb_instagram {
  padding-bottom: 0px !important;
}
.sbi_photo {
  border-radius: 10px;
}

.szkolenia-silcare {
  display: flex;
  align-items: center;
  justify-content: center;
  filter: invert(1);
  text-align: center;
  width: 100%;
  img {
    width: 200px;
  }
}
.card {
  img {
    height: 100%;
  }
}

.comment-section {
  .img-fluid {
    height:unset!important;
  }
}