.booksy {
  .wrapper {
    // height: 350px;
    display: flex;
    justify-content: flex;
    align-items: center;
    .button-wrapper {
      border-radius: 10px;
      padding: 10px;
      // color: #fff;
      background-image: url("https://images.unsplash.com/photo-1532611322744-369123d95bd3?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1170&q=80");
      background-repeat: no-repeat;
      // background-position: bottom left;
      // background-size: 70%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      // height: 550px;
      text-align: center;
      @media (max-width: 415px) {
        background-size: cover;
      }
      h5 {
        z-index: 1;
      }
      .text-wrapper {
        background-color: rgba(255, 255, 255, 0.8);
        padding: 10px;
        border-radius: 10px;
        box-shadow: 2px 2px 3px rgb(192, 192, 192);
      }
      .phone {
        position: absolute;
        width: 250px !important;
        rotate: 20deg;
        right: 56%;
        z-index: 0 !important;
        @media (max-width: 415px) {
          opacity: 0;
        }
      }
      img {
        width: 100px;
        margin-bottom: 5px;
        z-index: 2 !important;
      }
      .booksy-widget-container {
        z-index: 2;
      }
    }
  }
}
.silcare {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  img {
    width: 140px;
    margin-bottom: 5px;
  }
}
.comment-section {
  // background-color: #eaeaea;
  .icons {
    img {
      height: 150px;
    }
  }
  .card {
    border: none !important;
    background-color: #fcfcfc;
    border-radius: 10px !important;
    margin: 0 10px;
    // box-shadow: 1px 1px 10px rgb(177, 177, 177);
    // min-width: 40%;
    .card-body {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      background-image: white;
      border-radius: 10px;
    }
    .body-whom {
      @extend .body-title;
      text-align: left !important;
    }
    .body-title {
      font-size: 10px;
      font-weight: bold;
      color: #c9c9c9;
      text-align: right;
    }
    .body-text {
      padding: 5px 10px;
      // background-color: #ffffff;
      border-radius: 10px;
    }
    .body-image {
      img {
        height: 50px;
        width: 50px;
        border-radius: 10px;
      }
    }
    .body-stars {
      display: flex;
      justify-content: center;
      align-items: center;
      h5 {
        color: #c9c9c9;
        margin-right: 5px !important;
      }
      img {
        height: 20px;
        width: 20px;
      }
      svg {
        fill: #000000;
      }
    }
  }
}


.about {
  color: white !important;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  .about-photo {
    background-image: url("https://scontent-waw1-1.xx.fbcdn.net/v/t1.15752-9/348369113_1436088727179350_5481302135622626735_n.jpg?_nc_cat=101&ccb=1-7&_nc_sid=ae9488&_nc_ohc=231MU7IjO5sAX80slIB&_nc_ht=scontent-waw1-1.xx&oh=03_AdRPQTp5uolrf1C-tWpuEiFrRMPr7GI0t4lKaesI533uWw&oe=64A9BC05");
  }
}

.accordion-body {
  min-height: 90vh;
}