.wpcf7 {
  //   width: 100%;
  label {
    width: 100%;
  }
  input {
    border: none;
    background-color: #f4f4f3;
    padding: 5px;
    width: 100%;
    border: solid white 2px;
    border-radius: 10px;
    &::placeholder {
      font-size: 10px;
      padding-left: 5px;
    }
  }
  input[type="submit"] {
    font-family: $font-family-base !important;
  }
  .wpcf7-not-valid-tip {
    font-family: $font-family-base;
    font-weight: 300 !important;
  }
  textarea {
    // background-color: $white;
    padding: 5px;
    background-color: #f4f4f3;
    max-height: 300px;
    width: 100%;
    border: solid white 2px;
    border-radius: 10px;
    &::placeholder {
      font-size: 10px;
      padding-left: 5px;
      padding-top: 5px;
    }
  }
  .wpcf7-list-item {
    margin: 0px;
  }
  p {
    font-family: $font-family-base;
    // text-transform: uppercase;
    br {
      display: none;
    }
  }
}
.wpcf7-submit {
  height: 40px;
  background-color: #fc405c !important;
  color: white;
  text-transform: uppercase;
  border: solid white 2px;
  border-radius: 10px;
}
.form-control:focus {
  background-color: $light-gray !important;
  border-color: $secondary !important;
}
.form-check-input[type="checkbox"] {
  background-color: $dark-gray;
  border-radius: unset;
  border: none;
}
.form-check-input {
  border: none;
  background-color: $primary;
  &:focus {
    border: none;
  }
  &:checked {
    background-color: $secondary;
  }
}
