.about {
  // border-top: solid 3px black;
  font-family: $font-family-base;
  background-color: $light-gray;
  text-align: center;

  // border: 1px solid #ebebeb;
  background-color: white;
  p,
  h4 {
    color: #000000;
  }
  h4 {
    font-family: $font-family-logo;
  }
  img {
    // max-height: 150px;
    max-width: 120px;
    border-radius: 100%;
  }
  .brand-wrapper {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .col {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .card {
    color: white;
    background-color: #00000000;
    border: none;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    .card-img {
      min-height: 100%;
    }
    .card-img-overlay {
      border-radius: 10px;
    }
  }
  p {
    text-align: justify;
  }
  ul {
    list-style: none;
    li {
      a {
        color: #000000;
      }
    }
  }
}
