.video-container {
  border-bottom: $border-black;
}
.video-content {
  @media (max-width: 576px) {
    align-self: center;
  }
}

.about-container {
  border-bottom: $border-black;
  h2 {
    text-transform: uppercase;
  }
  p {
    font-size: $font-size-sm;
    text-align: justify;
  }
  a {
    color: black;
    font-size: $font-size-sm;
    font-weight: $font-weight-bold;
  }
}

.news-container {
  // background-color: black;
  border-radius: 10px;
  color: white;
  h2 {
    text-transform: uppercase;
  }
  @media (max-width: 576px) {
    flex-direction: column;
  }
  .news-card {
    border-radius: 10px;
    min-height: 400px;
    font-size: $font-size-sm;
    background-repeat: no-repeat;
    border: none;
    background-size: cover;
    justify-content: space-between;
    @media (max-width: 576px) {
      min-height: unset;
    }
    .card-body {
      border-radius: 10px;
      border: none;
      justify-content: space-between;
      display: flex;
      flex-direction: column;
      background-color: rgba(0, 0, 0, 0.301);
    }
    .card-text {
      p {
      }
    }
    h2 {
      color: $primary;
      text-align: center;
      font-family: $font-family-logo;
    }
    p {
      color: $primary;
      text-align: justify;
      padding: 0 50px;
      font-size: $font-size-base;
    }
    // .btn {
    //   height: 40px;
    //   background-color: #26cbc5;
    //   color: white;
    //   text-transform: uppercase;
    //   border: solid white 2px;
    //   border-radius: 10px;
    // }
  }

  .news-list {
    font-size: $font-size-sm;
    font-family: $font-family-base;
    color: white;
    h4 {
      font-family: $font-family-logo;
    }
    ul {
      list-style: none;
      padding-left: 0px;
      height: 100%;
      &:first-child {
      }
      li {
        position: relative;
        height: 25%;
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-blend-mode: darken;
        border: solid 3px white;

        &:last-of-type {
          border-radius: 0 0px 10px 10px !important;
        }
        a:first-of-type {
          text-transform: uppercase;
          margin: 0px;
          background-color: #f5f5f5;
        }
        p {
          margin-bottom: 1px;
        }
        a {
          position: relative;
          height: 100%;
          color: rgb(0, 0, 0);
          text-transform: uppercase;
          font-family: $font-family-logo !important;
          // font-size: inherit !important;
          text-align: center;
          justify-content: center;
          align-items: center;
          display: flex;
          border: none;
          border-radius: 10px;
          width: 100%;
          &:hover {
            background-color: #fc405c !important;
            transition: 200ms;
          }
        }
        a:nth-of-type(1) {
        }
        a.news {
          background-color: #f5f5f5;
          @extend h4;
          height: 100%;
          width: 100%;
          border-radius: 10px;
          &:hover {
            background-color: #fc405c !important;
            transition: 200ms;
          }
        }
      }
    }
  }
}
