.footer-container {
  background: $black;
  .footer {
    // min-height: 600px;
    color:white;
    h4 {
        // text-align: center;
    }
    a {
      padding-left: 10px;
    //   color: black;
      text-transform: uppercase;
      font-size:12px;
      font-weight: 900;
    }
    li {
      list-style: none;
      margin-top: 10px;
      height: 24px;
    }
    .icon.booksy {
      img {
        margin-left: -7px;
      }
    }
    .icon {
      img {
        filter: invert(1);
        height: 20px;
      }
    }
    .footer-nav {
      li:first-of-type {
        margin-top:0px;
      }
      li {
        padding: 0;
        margin-top: 10px;
        a {
          padding: 0;
        }
      }
    }
    .footer-brands {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            height:180px;
            padding:20px;
            @media (max-width: 576px) {
              height:110px;
            }
        }
    }
  }
}
.widget-area {
  display: flex !important;
  text-transform: uppercase;
}
.widget-item {
  padding: 10px 50px 0px 0px;
  font-weight: 600;
  font-size: $font-size-sm;
  ul {
    list-style: none;
    padding-left: 0.1rem;
  }
  h2 {
    font-size: $font-size-md;
  }
  a {
    color: $body-color;
  }
}
.widget-sidebar {
  display: flex;
  justify-content: flex-start;
  min-height: 200px;
  align-items: center;
}

.footer-logo {
  text-align: end;
  @media (max-width: 1190px) {
    text-align: center;
    padding-bottom: 50px;
  }
}

@media (max-width: 1190px) {
  nav {
    display: flex;
  }
  .navbar {
    text-align: center;
  }
  .widget-area {
    margin-top: 1rem;
    margin-bottom: 1rem;
    text-align: center;
    display: flex !important;
    flex-direction: column;
  }
  h4,li {
    text-align: center;
  }
  .widget-item {
    padding: 5px 0px 0px 5px;
    font-size: $font-size-sm;
  }
  .map {
    order:3
  }
  .contact {
    order:1
  }
  .short {
    order:2;
  }
}
