.custom-nav {
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 99999999999999999999999999999;
}
.icons-wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  ul {
    list-style: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    li {
      margin-left: 20px;
      width: auto;
      img {
        height: 15px;
        width: 100%;
      }
    }
  }
  button {
    margin-top: 3px;
    margin-left: 20px;
    border: none;
    background-color: #fff;
    a {
      font-family: $font-family-logo;
      color: #000000;
    }
    // height: 50px;
  }
}

.site-logo {
  display: flex;
  justify-content: start;
  align-items: center;
  a {
    color: black;
  }
  & img {
    height: 110px;
    width: auto;
    z-index: 999999;
    position: relative;
  }

  & span {
    font-family: $font-family-logo;
    font-size: 30px;
    font-weight: 900;
    z-index: 99999;
    position: relative;
    user-select: none;
  }
}

.nav-wrapper {
  display: flex;
  align-items: center;
  justify-content: end;
}
